.wrapper {
   min-height: 100vh;
   width: 100%;
   overflow: hidden;
   display: flex;
   flex-direction: column;
   align-items: center;
   background-color: #ffdf8c;
   padding-top: 40px;
}

.header {
   max-width: 1340px;
   width: 100%;
   height: 100px;
   background-color: #fff;
   border-radius: 13px;
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding: 0 15px;
   margin-bottom: 30px;
   &__login {
      font-size: 20px;
      padding: 6px 15px;
      background-color: #e8e6e6;
      font-family: "Proxima-Nova";
      font-weight: 400;
      color: #525151;
      border-radius: 15px;
      transition: all 0.3s;
      cursor: pointer;
      &:hover {
         color: #000;
         background-color: #ccc;
      }
      &:active {
         transform: translateY(5px);
      }
   }
   &__logo {
      display: flex;
      .header__img {
         margin-right: 15px;
         object-fit: contain;
      }
      .header__logo-text {
         // border: 1px solid black;
         .header__title {
            font-family: "Proxima-Nova";
            font-weight: 700;
            font-size: 25px;
            letter-spacing: 0.4px;
            line-height: 20px;
            margin-bottom: 6px;
            -webkit-text-stroke-color: #c7b299;
            -webkit-text-stroke-width: 0.4px;
            color: black;
         }
         .header__subtitle {
            color: #7b7b7b;
            font-size: 16px;
            letter-spacing: 0.1px;
         }
      }
   }
   &__cart {
      background-color: #fe5f1e;
      border-radius: 50px;
      position: relative;
      transition: all 0.3s;
      svg {
         position: absolute;
         top: 12px;
         left: 13px;
         cursor: pointer;
         stroke: #fff;
      }
      &-number {
         display: inline-block;
         padding: 13px 12px 12px 40px;
         color: #fff;
         font-size: 20px;
         font-family: "Proxima-Nova";
         font-weight: 700;
      }
      &:hover {
         background-color: #e3541b;
      }
      &:active {
         transform: translateY(-5px);
      }
   }
}

.main {
   max-width: 1340px;
   width: 100%;
   display: flex;
   flex-direction: column;
}

.main-pizza {
   display: flex;
   flex-direction: column;
   background-color: #fff;
   width: 100%;
   padding: 0 15px 0 15px;
   border-radius: 13px;
   &__navigation {
      width: 100%;
      height: 80px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 30px;
      .main-pizza__categories {
         max-width: 700px;
         width: 100%;
         height: 100%;
         display: flex;
         justify-content: space-between;
         align-items: center;
         .main-pizza__categories-button {
            font-size: 18px;
            padding: 10px 20px;
            background-color: #f9f9f9;
            font-family: "Proxima-Nova";
            font-weight: 700;
            color: #2c2c2c;
            border-radius: 20px;
            line-height: 21px;
            transition: background-color 0.4s, color 0.4s;
         }
         .main-pizza__categories-button:hover {
            background-color: #e8e8e8;
         }
         .main-pizza__categories-button._active {
            background-color: #282828;
            color: #fff;
         }
      }
      .main-pizza__sort {
         display: flex;
         align-items: center;
         position: relative;
         .main-pizza__sort-title {
            margin-right: 10px;
            font-family: "Proxima-Nova";
            font-weight: 700;
            color: #000;
            font-size: 16px;
         }
         .main-pizza__sort-select {
         }
      }
   }
   &__title {
      font-size: 28px;
      background-color: #f9f9f9;
      font-family: "Proxima-Nova";
      font-weight: 400;
      margin-bottom: 35px;
   }
   &__content {
      width: 100%;
      background-color: #fff;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-auto-flow: row;
      justify-items: center;
   }
}

.pizza-item {
   max-width: 290px;
   width: 100%;
   // border: 1px solid black;
   margin-bottom: 70px;
   &__picture {
      padding-left: 10px;
      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
   }
   &__content {
      font-family: "Proxima-Nova";
      display: flex;
      flex-direction: column;
      align-items: center;
      .pizza-item__title {
         width: 100%;
         // border: 1px solid black;
         font-weight: 700;
         font-size: 25px;
         text-align: center;
         margin-bottom: 20px;
      }
      .pizza-item__select-container {
         width: 100%;
         background-color: #f3f3f3;
         border-radius: 10px;
         margin-bottom: 15px;
         .pizza-item__type {
            display: flex;
            justify-content: center;
            padding: 5px 5px 0px 5px;
            margin-bottom: 3px;
            li {
               font-size: 15px;
               font-weight: 700;
               width: 50%;
               padding: 10px 0;
               border-radius: 5px;
               cursor: pointer;
               transition: background-color 0.4s, filter 0.4s;
            }
            li._active {
               background-color: #fff;
               filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.1));
            }
         }
         .pizza-item__size {
            display: flex;
            justify-content: center;
            padding: 0px 5px 5px 5px;
            li {
               font-size: 15px;
               font-weight: 700;
               width: 50%;
               padding: 10px 0;
               border-radius: 5px;
               cursor: pointer;
               transition: background-color 0.4s, filter 0.4s;
            }
            li._active {
               background-color: #fff;
               filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.1));
            }
         }
      }
      .pizza-item__bottom {
         width: 100%;
         display: flex;
         justify-content: space-between;
         align-items: center;
         .pizza-item__price {
            font-weight: 700;
            font-size: 25px;
            span {
               font-size: 20px;
            }
         }
         .pizza-item__button {
            color: #eb5a1e;
            padding: 12px 20px;
            font-weight: 700;
            font-size: 18px;
            line-height: 15px;
            background-color: #fff;
            border: 1px solid #eb5a1e;
            border-radius: 20px;
            transition: all 0.3s;
            &:hover {
               background-color: #eb5a1e;
               color: #fff;
            }
         }
      }
   }
}
