.main {
   max-width: 1340px;
   width: 100%;
   display: flex;
   flex-direction: column;
   align-items: center;
   background-color: #fff;
   padding: 50px 15px 50px 15px;
   border-radius: 13px;
}

.main-cart {
   max-width: 800px;
   width: 100%;
   .main-cart__top {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      margin-bottom: 70px;
      // display: none;
      .main-cart__title {
         display: flex;
         align-items: flex-end;
         .main-cart__title-icon {
            margin-right: 15px;
         }
         .main-cart__title-text {
            font-family: "Proxima-Nova";
            font-weight: 700;
            font-size: 35px;
            line-height: 25px;
         }
      }
      .main-cart__clear {
         cursor: pointer;
         display: flex;
         svg {
            padding-bottom: 2px;
            path {
               transition: stroke 0.3s;
            }
         }
         .main-cart__clear-text {
            color: #b6b6b6;
            font-size: 17px;
            margin-left: 8px;
            line-height: 21px;
            transition: color 0.3s;
         }
         &:hover {
            svg {
               path {
                  stroke: #000;
               }
            }
            .main-cart__clear-text {
               color: #000;
            }
         }
      }
   }
   .main-cart__bottom {
      // display: none;
      .main-cart__info {
         display: flex;
         justify-content: space-between;
         margin-bottom: 30px;
         .main-cart__total-products {
            font-family: "Proxima-Nova";
            font-size: 22px;
            span {
               font-weight: 700;
            }
         }
         .main-cart__total-price {
            font-family: "Proxima-Nova";
            font-size: 22px;
            span {
               font-weight: 700;
               color: #fe5f1e;
            }
         }
      }
      .main-cart__buttons {
         display: flex;
         justify-content: space-between;
         .main-cart__back {
            border: 2px solid #d3d3d3;
            background-color: #fff;
            font-size: 18px;
            font-weight: 700;
            padding: 15px 20px;
            color: #d3d3d3;
            border-radius: 25px;
            line-height: 15px;
            transition: all 0.4s;
            &:hover {
               border: 2px solid #000;
               background-color: #000;
               color: #fff;
            }
            &:active {
               transform: translateY(5px);
            }
         }
         .main-cart__pay {
            background-color: #fe5f1e;
            font-size: 18px;
            padding: 15px 20px;
            font-weight: 700;
            color: #fff;
            border-radius: 25px;
            line-height: 15px;
            transition: all 0.4s;
            &:hover {
               background-color: #e3541b;
            }
            &:active {
               transform: translateY(-5px);
            }
         }
      }
   }
   .main-cart__empty {
      border: 1px solid;
      display: flex;
      flex-direction: column;
      align-items: center;
      .main-cart__empty-text {
         text-align: center;
         margin-bottom: 50px;
         .main-cart__empty-title {
            font-family: "Proxima-Nova";
            font-size: 32px;
            font-weight: 700;
            margin-bottom: 10px;
            span {
               font-size: 25px;
            }
         }
         .main-cart__empty-subtle {
            font-size: 18px;
            color: #777777;
         }
         p:nth-child(2n) {
            margin-bottom: 5px;
         }
      }
      .main-cart__empty-picture {
         img {
         }
      }
   }
}

.cart-item {
   height: 80px;
   display: flex;
   justify-content: space-between;
   align-items: center;
   margin-bottom: 40px;
   &__info {
      display: flex;
      max-width: 350px;
      width: 100%;
      .cart-item__picture {
         max-width: 80px;
         width: 100%;
         max-height: 80px;
         margin-right: 15px;
         img {
            width: 100%;
            height: 100%;
            object-fit: contain;
         }
      }
      .cart-item__text {
         display: flex;
         flex-direction: column;
         justify-content: center;
         .cart-item__name {
            font-family: "Proxima-Nova";
            font-size: 22px;
            font-weight: 700;
            margin-bottom: 2px;
         }
         .cart-item__parameters {
            font-family: "Proxima-Nova";
            color: #8d8d8d;
            font-size: 18px;
            font-weight: 400;
         }
      }
   }
   &__quantity {
      display: flex;
      // border: 1px solid black;
      height: 32px;
      .cart-item__quantity-minus {
         width: 30px;
         height: 30px;
         border: 2px solid #fe5f1e;
         border-radius: 50%;
         position: relative;
         cursor: pointer;
         transition: all 0.3s;
         &:before {
            font-family: "IcoFont";
            content: "\ef9a";
            position: absolute;
            top: 4px;
            left: 4.5px;
            font-size: 18px;
            color: #fe5f1e;
         }
         &:hover {
            background-color: #fe5f1e;
            &:before {
               color: #fff;
            }
         }
         &:active {
            transform: translateY(5px);
         }
      }
      .cart-item__quantity-total {
         pointer-events: none;
         .cart-item__quantity-counter {
            font-size: 20px;
            padding: 0 5px;
            margin: 5px 10px 0 10px;
            font-weight: 700;
         }
         .cart-item__quantity-input {
            width: 0;
            height: 0;
         }
      }
      .cart-item__quantity-plus {
         width: 30px;
         height: 30px;
         border: 2px solid #fe5f1e;
         border-radius: 50%;
         position: relative;
         cursor: pointer;
         transition: all 0.3s;
         &:before {
            font-family: "IcoFont";
            content: "\efc2";
            position: absolute;
            top: 7px;
            left: 7px;
            font-size: 13px;
            color: #fe5f1e;
         }
         &:hover {
            background-color: #fe5f1e;
            &:before {
               color: #fff;
            }
         }
         &:active {
            transform: translateY(-5px);
         }
      }
   }
   &__price {
      font-family: "Proxima-Nova";
      font-size: 22px;
      span {
         font-size: 18px;
         font-weight: 400;
      }
   }
   &__delete {
      width: 30px;
      height: 30px;
      border: 2px solid #d7d7d7;
      border-radius: 50%;
      position: relative;
      cursor: pointer;
      transition: all 0.3s;
      &:before {
         font-family: "IcoFont";
         content: "\eee4";
         position: absolute;
         top: 4px;
         left: 4px;
         font-size: 18px;
         color: #d7d7d7;
      }
      &:hover {
         border: 2px solid #000;
         background-color: #000;
         &:before {
            color: #fff;
         }
      }
      &:active {
         transform: translateY(5px);
      }
   }
}
