.dropcontainer {
   position: absolute;
   top: 180%;
   right: 0;
   font-size: 16px;
   font-family: "Proxima-Nova";
   font-weight: 400;
   color: #525151;
   filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.1));
   ul {
      font-size: 16px;
      border-top: none;
      background: #fff;
      list-style-type: none;
      padding: 0;
      margin: 0;
      width: 100%;
      z-index: 100;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      li {
         padding: 10px 15px;
         -webkit-transition: all 0.5s ease;
         -moz-transition: all 0.5s ease;
         -o-transition: all 0.5s ease;
         transition: all 0.5s ease;
         cursor: pointer;
         &:hover {
            background: #f5f5f5;
            outline: none;
            background-color: #fe5f1e0d;
         }
         &:last-child {
            border-bottom: none;
         }
         a {
            font-size: 16px;
            font-family: "Proxima-Nova";
            color: #000;
         }
      }
   }
}
.trigger {
   font-family: "Proxima-Nova";
   font-weight: 400;
   color: #e3541b;
   border-bottom: 1px solid #e3541b;
   border-style: dashed;
   font-size: 16px;
   width: 100%;
   display: block;
   -webkit-box-sizing: border-box;
   -moz-box-sizing: border-box;
   box-sizing: border-box;
   -webkit-transition: all 0.5s ease;
   -moz-transition: all 0.5s ease;
   -o-transition: all 0.5s ease;
   transition: all 0.5s ease;
   &:hover {
      border-bottom: none;
   }
}
.activetrigger {
   font-family: "Proxima-Nova";
   font-weight: 400;
   color: #e3541b;
   font-size: 16px;
   width: 100%;
   display: block;
   -webkit-box-sizing: border-box;
   -moz-box-sizing: border-box;
   box-sizing: border-box;
}
.dropdownhidden {
   display: none;
}
.dropdownvisible {
   height: auto;
}
